import React, { useState } from 'react';
import { Animate } from 'react-move'
import styled, { keyframes } from 'styled-components'

import HomePage from './containers/HomePage'
import StepPreloader from './containers/StepPreloader'
import preload from './preload'

import Box from './components/Box'
import Text from './components/Text'

const flicker = keyframes`
10%, 90% { opacity: 1 }
50% { opacity: 0.2 }
`

const Loading = styled(Text.h2)`
  animation: ${flicker} 1.5s infinite;
`

function App() {
  const [inited, setLoaded] = useState()
  return (
    <>
      {inited && <HomePage />}
      <StepPreloader list={preload} onFirstLoaded={() => setLoaded(true)} />
      <Animate
        show={!inited}
        start={{
          opacity: 1,
        }}
        enter={{
          opacity: [1],
        }}
        leave={{
          opacity: [0],
        }}
      >
        {state => (
          <Box
            position="absolute"
            top="0"
            left="0"
            right="0"
            bottom="0"
            bg="#16221d"
            style={state}
            pointerEvents="none"
          >
            <Box position="absolute" top="50%" left="50%" transform="translate(-50%, -50%)" color="primary">
              <Loading>Loading...</Loading>
            </Box>
          </Box>
        )}
      </Animate>
    </>
  );
}

export default App;
